<template>
  <div class="NDT-ResetPassword">
    <h1 class="NDT-ResetPassword__Header">
      Tilbakestill passord
    </h1>
    <div class="NDT-ResetPassword__Body">
      <b-container>
        <b-form @submit.prevent="resetPassword">
          <b-form-group>
            <b-input-group>
              <b-alert variant="danger" :show="showAlerts && !passwordValid">Passord må være minst 7 tegn</b-alert>
            </b-input-group>
            <b-input-group>
              <b-input id="password" placeholder="Nytt passord" v-model="password" type="password" />
            </b-input-group>
          </b-form-group>
          <b-form-group>
            <b-input-group>
              <b-alert variant="danger" :show="showAlerts &&  !passwordsValid">Passordene stemmer ikke overens</b-alert>
            </b-input-group>
            <b-input-group>
              <b-input id="passwordRepeat" placeholder="Gjenta nytt passord" v-model="passwordRepeat" type="password" />
            </b-input-group>
          </b-form-group>
          <b-form-group>
            <button
              class="NDT-ResetPassword__submit btn btn-lg btn-block"
              type="submit"
              :disabled="!formValid"
            >
              <template v-if="!isResetting">
                Endre passord
              </template>
              <template v-else>
                <icon icon="spinner" spin />
                <span class="NDT-ResetPassword__spinnerText">
                  Endrer passord
                </span>
              </template>
            </button>
          </b-form-group>
        </b-form>
        <b-alert variant="danger" :show="showError">
          Beklager, det oppstod en feil. Vennligst prøv igjen
        </b-alert>
        <b-alert variant="success" :show="showSuccess">
          Passordet ditt skal nå være tilbakestilt.
        </b-alert>
      </b-container>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'ResetPassword',

    props: {
      token: {
        type: String,
        default: null
      }
    },

    data: _ => ({
      isResetting: false,
      password: '',
      showError: false,
      showSuccess: false,
      passwordRepeat: ''
    }),

    computed: {
      showAlerts () {
        return this.password.length || this.passwordRepeat.length
      },

      passwordValid () {
        return this.password.length && this.password.length > 6
      },

      passwordsValid () {
        return this.passwordValid && this.password === this.passwordRepeat
      },

      formValid () {
        return this.passwordsValid
      }
    },

    methods: {
      async resetPassword () {
        this.isResetting = true
        try {
          await this.$api.post(`reset/${this.token}`, {
            password: this.password
          })
          this.showError = false
          this.showSuccess = true
        } catch (error) {
          console.error(error)
          this.showError = true
          this.showSuccess = false
        }
        this.isResetting = false
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../assets/scss/variables";

  .NDT-ResetPassword {
    color: $white;
    border-radius: .25rem;
    background: $light-gray;
    border: 1px solid $dark-gray;
    padding: .5rem 1rem .5rem 1rem;

    &__Header {
      color: $lime;
      padding-bottom: 1rem;
      margin-bottom: 1rem;
      border-bottom: 1px solid $dark-gray;
    }

    &__submit {
      background-color: $lime;
      color: $dark-gray;
    }

    &__spinnerText {
      margin-left: 1rem;
    }
  }
</style>
